/* ---------------------------------- */
/* General Reset */
/* ---------------------------------- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100%;
  font-family: Arial, Helvetica, sans-serif;
  scroll-behavior: smooth;
  background: transparent;
  color: #333;
}

/* ---------------------------------- */
/* App Wrapper */
/* ---------------------------------- */
.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* Background handled in App.js inline style */
}

/* Add a slight overlay blur for readability */
.app::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(4px);
  z-index: -1;
}

/* ---------------------------------- */
/* Header */
/* ---------------------------------- */
.header {
  background: rgba(0, 77, 64, 0.85);
  color: #fff;
  text-align: center;
  padding: 20px;
  backdrop-filter: blur(6px);
}

.header .brand {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
}

.logo {
  height: 60px;
}

.header h1 {
  font-size: 1.8rem;
}

.header p {
  font-size: 1rem;
}

/* ---------------------------------- */
/* Sections */
/* ---------------------------------- */
.section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  padding: 40px 20px;
  background: rgba(255, 255, 255, 0.85);
  border-bottom: 1px solid #e0e0e0;
  flex-wrap: wrap;
  transition: all 0.3s ease;
}

.section.alt {
  background: rgba(240, 240, 240, 0.85);
}

.section-content {
  flex: 1;
  min-width: 280px;
}

.section h2 {
  margin-bottom: 15px;
  color: #004d40;
}

.section p {
  line-height: 1.6;
  font-size: 1rem;
}

.section-image-wrapper {
  flex: 1;
  display: flex;
  justify-content: center;
  min-width: 280px;
}

.section-image {
  max-width: 100%;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.2);
  transition: transform 0.3s ease;
}

.section-image:hover {
  transform: scale(1.05);
}

/* ---------------------------------- */
/* Nursery Gallery */
/* ---------------------------------- */
.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 15px;
  margin-top: 20px;
}

.gallery-grid img {
  width: 100%;
  height: auto;
  cursor: pointer;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.gallery-grid img:hover {
  transform: scale(1.05);
}

/* Add touch-friendly swipe effect */
.gallery-grid {
  overflow-x: auto;
  display: flex;
  flex-wrap: nowrap;
  padding-bottom: 10px;
}

.gallery-grid img {
  flex: 0 0 auto;
  margin-right: 10px;
}

/* ---------------------------------- */
/* Lightbox */
/* ---------------------------------- */
.lightbox-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.85);
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

.lightbox-overlay.active {
  display: flex;
}

.lightbox-overlay img {
  max-width: 90%;
  max-height: 90%;
  border-radius: 10px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.5);
}

/* ---------------------------------- */
/* Footer */
/* ---------------------------------- */
.footer {
  text-align: center;
  padding: 15px;
  background: rgba(0, 77, 64, 0.85);
  color: #fff;
  margin-top: auto;
  backdrop-filter: blur(6px);
}

/* ---------------------------------- */
/* Responsive Adjustments */
/* ---------------------------------- */
@media (max-width: 992px) {
  .section {
    flex-direction: column;
    text-align: center;
  }

  .section-image-wrapper {
    margin-top: 20px;
  }

  .gallery-grid {
    flex-wrap: nowrap;
    overflow-x: auto;
  }
}

@media (max-width: 576px) {
  .header h1 {
    font-size: 1.5rem;
  }

  .section h2 {
    font-size: 1.2rem;
  }

  .section p {
    font-size: 0.95rem;
  }

  .section-image {
    max-width: 100%;
  }

  .gallery-grid img {
    min-width: 120px;
    margin-right: 10px;
  }
}
